bizagi.jqxhrList = (function () {
    var self = this;
    self._bufferRendering = [];
    self._intercept = false;
    return {
        intercept: self._intercept,
        add: function (params) {
            if (params.id && params.source && params.url && params.req) {
                self._bufferRendering.push(params);
            }
        },
        delete: function (id) {
            self._bufferRendering = self._bufferRendering.filter(function (item) {
                return item.id !== id;
            });
        },

        abortAll: function (source) {
            $.each(self._bufferRendering, function (_, item) {
                if (item.source === source) {
                    item.req.abort();
                }
            });
        }
    };
})();


$.ajaxPrefilter(function (options, _, jqxhr) {
    if(bizagi.jqxhrList.intercept && options.interceptor && options.interceptor.intercept) {
        var _id = Math.guid();

        bizagi.jqxhrList.add({
            id: _id,
            source: options.interceptor.source,
            url: options.url,
            req: jqxhr
        });
        
        jqxhr.always(function () {
            bizagi.jqxhrList.delete(_id);
        });
    }
});
