bizagi.util.isString = function (value) {
    return Object.prototype.toString.apply(value) === "[object String]";
};
bizagi.util.isObject = function (value) {
    return Object.prototype.toString.apply(value) === "[object Object]";
};
bizagi.util.isArray = function (value) {
    return Array.isArray(value);
};
bizagi.util.isNull = function (value) {
    return Object.prototype.toString.apply(value) === "[object Null]";
};

bizagi.util.isBoolean = function (value) {
    return Object.prototype.toString.apply(value) === "[object Boolean]";
};

/**
 * Decode data from encodeURI charset
 * @param {} value
 * @returns {}
 */
bizagi.util.decodeURI = function (value) {
    value = value || "";
    var finishDecoded = false;
    var decodedValue = value;
    var infinityControl;

    // Try to decode data value
    while (!finishDecoded) {
        try {
            infinityControl = decodeURI(decodedValue);
            if (infinityControl == decodedValue) {
                finishDecoded = true;
            } else {
                decodedValue = infinityControl;
            }
        } catch (e) {
            finishDecoded = true;
        }
    }

    return decodedValue;
};

/**
 * Determines if a control as a scrollbar
 * @param {} element
 * @param {} direction
 * @returns {}
 */
bizagi.util.hasScroll = function (el, direction) {
    if (el.length)
        el = el[0];
    direction = (direction === 'vertical') ? 'scrollTop' : 'scrollLeft';
    var result = !!el[direction];
    if (!result) {
        el[direction] = 1;
        result = !!el[direction];
        el[direction] = 0;
    }
    return result;
};

/**
 * Lose focus of all modal elements and closes them.
 */
bizagi.util.loseFocus = function () {
    if (!!$.mobiscroll.activeInstance)
        $.mobiscroll.activeInstance.cancel();

    $(":focus").blur();

    if ($(".km-modalview") && typeof ($(".km-modalview").data("kendoMobileModalView")) !== "undefined")
        $(".km-modalview").data("kendoMobileModalView").close();
    if ($(".km-actionsheet") && typeof ($(".km-actionsheet").data("kendoMobileActionSheet")) !== "undefined")
        $(".km-actionsheet").data("kendoMobileActionSheet").close();
};

/**
 * Validate feature support depending on server channel and server version
 * @param {} feature
 * @returns {}
 */
bizagi.util.validateFeatureSupport = function (feature) {
    var currentChannel = bizagi.util.getChannelType().toUpperCase();
    var supportedVersion;

    switch (feature) {
        case "filesCount":
        case "filesList":
        case "discussionCase":
            supportedVersion = bizagi.util.compatibilityCaseFolder[currentChannel];
            break;
        case "advancedSearch":
            supportedVersion = bizagi.util.compatibilityAdvancedSearch[currentChannel];
            break;
        case "taskColorState":
            supportedVersion = bizagi.util.compatibilityTaskColorState[currentChannel];
            break;
        case "commitRollbackActions":
            supportedVersion = bizagi.util.compatibilityCommitRollbackActions[currentChannel];
            break;
        default:
            return false;
    }

    return supportedVersion ? bizagi.version.server.greaterThanOrEqualsTo(supportedVersion) : false;
};

/**
 * Check data url
 * @param {} data
 * @returns {}
 */
bizagi.util.isDataUrl = function (data) {
    var regex =
        /^\s*data:([a-z]+\/[a-z0-9-+.]+(;[a-z-]+=[a-z0-9-]+)?)?(;base64)?,([a-z0-9!$&',()*+;=\-._~:@\/?%\s]*)\s*$/i;

    try {
        return !!(data.toString()).match(regex);
    } catch (error) {
    }

    return false;
};

/**
 * Get valid data url
 * @param {} data
 * @returns {}
 */
bizagi.util.getDataUrl = function (properties) {
    var currentUrl;

    try {
        if (typeof (properties.value) !== "undefined" && !bizagi.util.isEmpty(properties.value)) {
            var data = properties.value;
            var url = data.value || data;

            if (Array.isArray(url)) {
                var tempUrl = url.shift();
                currentUrl = Array.isArray(tempUrl) ? tempUrl.join("") : tempUrl;
            } else {
                currentUrl = url;
            }
        } else {
            currentUrl = properties.url || "";
        }

        currentUrl = !bizagi.util.isEmpty(currentUrl) ? "data:image/png;base64," + currentUrl.replace(/^data:.*;base64,/, "") : "";

        if (currentUrl && currentUrl.indexOf("Invalid case id") !== -1)
            currentUrl = "";

    } catch (error) {
        currentUrl = "";
    }

    return currentUrl;
};


/**
 * Enables accesory bar
 */
if (bizagi.util.isNativePluginSupported() && window.cordova && window.cordova.plugins.Keyboard) {
    cordova.plugins.Keyboard.hideKeyboardAccessoryBar(false);
}

/**
* Gets the final message when the user is offline or online (In Offline forms)
* @param {} state 
* @returns {} The corresponding message dependieng on the case
*/

bizagi.util.getMessageFromNetworkState = function (state, action) {
    var resource = "render-without-globalform-offline";
    var defaultOfflineMessage = "Your offline case has been created successfully. It will be synchronized as soon as you go online.";

    if(action){
        resource += "-" + action;
        switch(action){
            case "save":
                defaultOfflineMessage= "Your offline case has been saved successfully. It will be synchronized as soon as you go online.";
                break;
            case "next":
                defaultOfflineMessage= "Your offline case has been advanced successfully. It will be synchronized as soon as you go online.";
                break;
        }
    }

    if (state) {
        return bizagi.localization.getResource(resource + "-userOnline" , "The case has been created successfully, please wait until the server synchronizes");
    } else {
        return bizagi.localization.getResource(resource, defaultOfflineMessage);
    }
};

bizagi.util.sanitizeHTML = function (str) {
	var temp = document.createElement('div');
	temp.textContent = str;
	return temp.innerHTML;
};

bizagi.util.arrayUnique = function (array) {
    var items = array.concat();

    for (var i = 0, length = items.length; i < length; ++i) {
        for (var j = i + 1; j < length; ++j) {
            if (items[i] === items[j]) {
                items.splice(j -= 1, 1);
            }

        }
    }

    return items;
};

bizagi.util.getMaxZIndex = function (selector) {
    selector = selector || $("div,span");
    return Math.max.apply(null, $(selector).map(function () {
        var zIndex;
        return isNaN(zIndex = parseInt($(this).css("z-index"), 10)) ? 0 : zIndex;
    }));
};


(function ($) {
    jQuery.getMaxZindex = function () {
        var elm = $("*");
        var maxZindex = 1;

        $.each(elm, function (key, value) {
            var zindex = parseInt($(value).css("z-index"));
            if (zindex > maxZindex) {
                maxZindex = zindex + 1;
            }

        });

        return maxZindex;
    }
})(jQuery);

/**
 *   Generates a random guid
 */
Math.guid = function () {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
};

/**
 * Convert String to Base64 Unicode
 * @param {} str 
 * @returns {} 
 */
bizagi.util.b64EncodeUnicode = function (string) {
    return btoa(encodeURIComponent(string).replace(/%([0-9A-F]{2})/g, function (match, p1) {
        return String.fromCharCode("0x" + p1);
    }));
};

/**
 * Validate if the mobile image editor is enabled
 * @returns {}
 */
bizagi.util.isNativeImageEditorSupported = function () {
    return bizagi.util.isNativePluginSupported() && bizagi.util.isiOSDevice() && bizagi.override.enableImageEditor;
};

bizagi.util.isOfflineFormsV2Supported = function() {
    var minSupportedVersion = "11.2.2.36";
    var isCorrectDevice = bizagi.util.isCordovaSupported() || ("" + BIZAGI_ENVIRONMENT).toLowerCase() === "debug";
    return isCorrectDevice && bizagi.version.server.greaterThanOrEqualsTo(minSupportedVersion);
};

/*
 *   Loads a file on-demand
 */
bizagi.util.loadFile = function (params) {
    var defer = new $.Deferred();
    var url = params && params.src ? params.src : params;
    var type = params && params.type ? params.type : null;

    if (!bizagi.loader) {
        defer.reject();
    } else{
        bizagi.loader.loadFile({
            src: url,
            type: type
        }) .then(function() {
            defer.resolve();
        });
    }

    return defer.promise();
};

/**
 * Replace line breaks for html line breaks
 * @param data
 * @returns {string}
 */
bizagi.util.decodedDisplayValue = function(data) {
    var value = data || "";
    var valueToDisplay = "";

    if (typeof (value) === "string") {
        var decodedDisplayValue = bizagi.util.decodeURI(value);

        valueToDisplay = decodedDisplayValue.replaceAll("&", "&amp;");
        valueToDisplay = valueToDisplay.replaceAll("$", "&#36;");
        valueToDisplay = valueToDisplay.replaceAll("<", "&lt;").replaceAll(">", "&gt;");
        valueToDisplay = valueToDisplay.replaceAll("\"", "&#34;").replaceAll("'", "&#39;").replaceAll("/", "&#47;");
        valueToDisplay = valueToDisplay.replaceAll("\\n", "<br/>");
        valueToDisplay = valueToDisplay.replaceAll("\n", "<br/>");
    }

    return valueToDisplay;
};


/**
 * To escape values
 * @param {} value 
 * @returns {} 
 */
bizagi.util.escapeRegex = function(value) {
    value = value || "";
    return value.replace(/([\^\$\(\)\[\]\{\}\*\.\+\?\|\\])/gi, "\\$1");
};

/**
 * Function to get a featureObject by path within the global bizagiConfig.features object. Example: "userService.v1.enabled"
 */
bizagi.util.getFeatureObject = function (path) {
    var arr = path.split('.');
    this.featureObjectCache = this.featureObjectCache || {};

    function getFeatureObjectIte(arr) {
        var features = bizagi.util.getConfigFeatures();

        if (typeof path !== "string" || typeof features == "undefined") return;

        var pivot = features;
        for (var i = 0, len = arr.length; i < len; i++) {
            pivot = pivot[arr[i]];
            if (typeof pivot === "undefined") return;
        }
        return pivot;
    };
    if (path in this.featureObjectCache) {
        return this.featureObjectCache[path];
    } else {
        return this.featureObjectCache[path] = getFeatureObjectIte(arr);
    }
};

/**
 * Load features from mobile
 */
bizagi.util.getConfigFeatures = function () {
    var bizagiConfiguration = window.bizagiConfig || bizagiConfig || {};

    if(bizagi.util.isMobileDevice() && typeof(BIZAGI_MOBILE_FEATURES) !== "undefined" && !bizagi.util.isEmpty(BIZAGI_MOBILE_FEATURES)) {
        bizagiConfiguration.features = JSON.parse(BIZAGI_MOBILE_FEATURES || "{}")
    }

    return bizagiConfiguration.features;
};

/**
 * Reset features flag
 */
bizagi.util.resetConfigFeatures = function () {
    bizagi.util.featureObjectCache = undefined;
};


/***
 * Transform base64 image to blob
 * @b64Data 
 * @contentType 
 * @sliceSize 
 */
bizagi.util.b64toBlob = function(b64Data, contentType, sliceSize) {
    contentType = contentType || '';
    sliceSize = sliceSize || 512;

    var byteCharacters = atob(b64Data);
    var byteArrays = [];

    for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
        var slice = byteCharacters.slice(offset, offset + sliceSize);

        var byteNumbers = new Array(slice.length);
        for (var i = 0; i < slice.length; i++) {
            byteNumbers[i] = slice.charCodeAt(i);
        }

        var byteArray = new Uint8Array(byteNumbers);

        byteArrays.push(byteArray);
    }

    var blob = new Blob(byteArrays, { type: contentType });
    return blob;
};

bizagi.util.blobTob64 = function( blob, onlyData){
    var def = new $.Deferred();
    var reader = new FileReader();
    reader.readAsDataURL(blob); 
    reader.onloadend = function() {
        base64data = reader.result;
        if(onlyData){
            base64data = base64data.split(',')[1];
        }
        def.resolve(base64data);
    }
    return def;
}

// Encodes the xpath 
bizagi.util.encodeXpath = function(xpath) {
    if (bizagi.util.isEmpty(xpath) || typeof xpath.replaceAll === "undefined")
        return "";
    return xpath.replaceAll(".", "_").replaceAll("=", "_").replaceAll("[", "_").replaceAll("]", "_");
};
/**
 * Check fail request
 * @param result
 * @returns {*|boolean}
 */
bizagi.util.isFailResponse = function (result) {
    return result && ((result.type && result.type === "error") || (result.status && result.status === "error") || (result.code && result.code === "UNKNOWN_ERROR"));
};

/**
 * Obtain the hash from a string
 * https://stackoverflow.com/questions/7616461/generate-a-hash-from-string-in-javascript
 */
String.prototype.hashCode = function() {
    var hash = 0, i, chr;
    if (this.length === 0) return hash;
    for (i = 0; i < this.length; i++) {
      chr   = this.charCodeAt(i);
      hash  = ((hash << 5) - hash) + chr;
      hash |= 0; // Convert to 32bit integer
    }
    return hash;
};

